"use client";
import { useState, useEffect } from "react";
import { FaPlus, FaMinus } from "react-icons/fa6";

interface CounterProps {
	value: number;
	onChange: (newValue: number) => void;
	min?: number;
	max?: number;
}

export default function Counter({
	value,
	onChange,
	min = 1,
	max = 10,
}: CounterProps) {
	const [count, setCount] = useState(value);

	// Sincroniza o estado interno com o valor externo
	useEffect(() => {
		setCount(value);
	}, [value]);

	const handleDecrement = () => {
		const newValue = Math.max(min, count - 1);
		setCount(newValue);
		onChange(newValue);
	};

	const handleIncrement = () => {
		const newValue = Math.min(max, count + 1);
		setCount(newValue);
		onChange(newValue);
	};

	// Botões desabilitados quando nos limites
	const isMin = count <= min;
	const isMax = count >= max;

	return (
		<div className="flex items-center justify-center">
			<button
				className={`text-2xl font-bold w-8 h-8 flex items-center justify-center ${
					isMin
						? "text-gray-400 cursor-not-allowed"
						: "text-black hover:bg-gray-100"
				} rounded-full`}
				onClick={handleDecrement}
				disabled={isMin}
				aria-label="Diminuir quantidade"
			>
				<FaMinus />
			</button>
			<div className="text-2xl font-bold min-w-[2rem] text-center mx-2">
				{count}
			</div>
			<button
				className={`text-2xl font-bold w-8 h-8 flex items-center justify-center ${
					isMax
						? "text-gray-400 cursor-not-allowed"
						: "text-black hover:bg-gray-100"
				} rounded-full`}
				onClick={handleIncrement}
				disabled={isMax}
				aria-label="Aumentar quantidade"
			>
				<FaPlus />
			</button>
		</div>
	);
}
